@import url(https://use.typekit.net/gcx6nic.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{background-color:#00695f;color:white}

h1{text-align:center;font-family:'dunbar-low', sans-serif;font-weight:300;font-style:normal}h2{font-family:'dunbar-low', sans-serif;font-weight:300;font-style:normal}.logo{text-align:center;font-family:'fave-script-pro', sans-serif;font-weight:400;font-style:normal;font-size:5em;padding:0;margin:0em;-webkit-text-stroke:#AB215C 0.5px;padding-top:0.5em}

